* {
    box-sizing: border-box;
}

body {
    background-color: #d2dbdc;
    margin: 0;
    font-family: "Segoe UI", Helvetica Neue, Helvetica, Lucida Grande,
        Arial;
}

.app-window {
    display: flex;
    height: 100vh;
    background-color: #ededed;
}
.sidebar {
    width: 35%;
    max-width: 415px;
    display: flex;
    flex-direction: column;
    border-right: 1px solid #ddd;
}
header {
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
}
.header--avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
}
.header--buttons {
    display: flex;
}
.header--btn {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.search {
    background-color: #f6f6f6;
    border-bottom: 1px solid #eee;
    padding: 5px 15px;
}

.search--input {
    background-color: #fff;
    height: 40px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    padding: 0 10px;
}
.search--input input {
    flex: 1;
    border: 0;
    outline: 0;
    background-color: transparent;
    margin-left: 10px;
}
.chatList {
    flex: 1;
    background-color: #fff;
    overflow-y: auto;
}
.chatList::-webkit-scrollbar {
    width: 6px;
    height: 6px;
}
.chatList::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.2);
}
.contentarea {
    flex: 1;
}
